.CreateTodoButton {
	background-color: #0669f7;
	box-shadow: 0px 0px 25px rgba(6, 105, 247, 0.5);
	border: none;
	border-radius: 50%;
	cursor: pointer;
	font-size: 50px;
	position: fixed;
	bottom: 24px;
	right: 24px;
	font-weight: bold;
	color: #fafafa;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 64px;
	width: 64px;

	font-family: "Poppins", sans-serif;

	transform: rotate(0);
	transition: 0.3s ease;
}

.CreateTodoButton:hover {
	transform: rotate(180deg);
}

